import {
    Routes,
    Route,
} from "react-router-dom";
import DashboardLayout from "./components/layout/DashboardLayout";
import Home from "./app/page";
import AuthPage from "./app/auth/page";
import ForgotPasswordPage from "./app/auth/forgot-password/page";
import ResetPasswordPage from "./app/auth/reset-password/page";
import VerifyEmailPage from "./app/auth/verify-email/page";
import DashboardOverview from "./app/dashboard/page";
import ApiKeysPage from "./app/dashboard/api-keys/page";
import BacktestingPage from "./app/dashboard/backtesting/page";
import BotsPage from "./app/dashboard/bots/page";
import PerformancePage from "./app/dashboard/performance/page";
import ProfilePage from "./app/dashboard/profile/page";
import ReferralsPage from "./app/dashboard/referrals/page";
import SubscriptionPage from "./app/dashboard/subscription/page";
import SupportPage from "./app/dashboard/support/page";

import PublicRoute from "./components/auth/PublicRoute";
import PrivateRoute from "./components/auth/PrivateRoute";

const App = () => {
    return (
        <>
            <Routes>
                <Route element={<PublicRoute />}>
                    <Route path="auth" element={<AuthPage />} />
                    <Route path="auth/forgot-password" element={<ForgotPasswordPage />} />
                    <Route path="auth/reset-password" element={<ResetPasswordPage />} />
                    <Route path="auth/verify-email" element={<VerifyEmailPage />} />
                </Route>
                <Route element={<PrivateRoute />}>
                    <Route element={<DashboardLayout />}>
                        <Route path="dashboard" element={<DashboardOverview />} />
                        <Route path="dashboard/api-keys" element={<ApiKeysPage />} />
                        <Route path="dashboard/backtesting" element={<BacktestingPage />} />
                        <Route path="dashboard/bots" element={<BotsPage />} />
                        <Route path="dashboard/performance" element={<PerformancePage />} />
                        <Route path="dashboard/profile" element={<ProfilePage />} />
                        <Route path="dashboard/referrals" element={<ReferralsPage />} />
                        <Route path="dashboard/subscription" element={<SubscriptionPage />} />
                        <Route path="dashboard/support" element={<SupportPage />} />
                    </Route>
                    
                </Route>

                <Route path="/" element={<Home />} />
            </Routes>
        </>
    );
}

export default App;