import { useCallback } from "react";
import { useNavigate } from 'react-router-dom';

export default function Home() {
  const navigate = useNavigate();

  const handleClick = useCallback(() => {
    navigate('/auth');
  }, [navigate]);

  return (
    <div className="flex flex-col items-center justify-center gap-8">
      <h1 className="text-4xl font-bold text-black mb-2">Trading Bot Platform</h1>
      <p className="text-lg text-accent mb-6">Automate your trading with style and security.</p>
      <button
        className="px-6 py-3 rounded-lg bg-black text-white font-semibold shadow-subtle hover:shadow-strong transition-all"
        onClick={handleClick}
      >
        Get Started
      </button>
    </div>
  );
}
